.logInContainer {
  font-family: 'Roboto';
  & .title {
    margin: 0 0 47px 0;
    font-weight: 700;
    font-size: 48px;
    line-height: 56px;
    letter-spacing: 0.02em;
    color: rgba(255, 255, 255, 0.9);
  }
  & .options {
    margin: 0 0 67px 0;
    display: flex;
    gap: 30px;
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
    letter-spacing: 0.02em;
    color: rgba(255, 255, 255, 0.9);
  }
  .inputWithLabel {
    margin: 0 0 30px 0;
    position: relative;
    .label {
      display: block;
      margin: 0 0 9px 0;
      font-weight: 500;
      font-size: 15px;
      line-height: 18px;
      letter-spacing: 0.02em;
      color: rgba(255, 255, 255, 0.6);
    }
    .eye {
      cursor: pointer;
      user-select: none;
      width: 24px;
      height: 24px;
      position: absolute;
      top: 42px;
      right: 15px;
    }
  }
  .button {
    cursor: pointer;
    user-select: none;
    width: 452px;
    height: 54px;
    margin: 0 0 10px 0;
    background: #5b549d;
    border-radius: 3px;
    font-weight: 500;
    font-size: 24px;
    line-height: 54px;
    letter-spacing: 0.02em;
    color: rgba(255, 255, 255, 0.9);
    text-align: center;
    &:hover {
      opacity: 0.5;
    }
  }

  .signIn {
    margin: 30px 0 0 0;
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
    letter-spacing: 0.02em;
    color: rgba(255, 255, 255, 0.9);
    text-align: right;
    span {
      cursor: pointer;
      user-select: none;
      color: #7f75e3;
      &:hover {
        opacity: 0.5;
      }
    }
  }
  .referralCode {
    display: flex;
    align-items: center;
    font-size: 15px;
    line-height: 18px;
    letter-spacing: 0.02em;
    color: #7f75e3;
    margin: -20px 0 29px 0;
    .arrow {
      width: 15px;
      height: 7.5px;
      margin: 0 0 0 10px;
      background-image: url('../../assets/img/referral-arrow.svg');
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
    }
  }
  .checkContainer {
    margin: 0 0 29px 0;
    display: flex;
    align-items: center;
    .terms {
      display: flex;
      align-items: center;
      font-family: 'Roboto';
      font-weight: 500;
      font-size: 15px;
      line-height: 18px;
      letter-spacing: 0.02em;
      color: rgba(255, 255, 255, 0.6);
      position: relative;
      & .check {
        visibility: hidden;
      }
      .check + label:before {
        content: '';
        position: absolute;
        left: 0;
        height: 14px;
        width: 14px;
        display: inline-block;
        border: 1px solid #333;
      }
      .check:checked + label:before {
        content: '';
        background: rgb(0, 0, 0);
        background-image: url('../../assets/img/check.svg');
        background-position: center;
        background-repeat: no-repeat;
        background-size: contain;
      }
      label {
        cursor: pointer;
        user-select: none;
        padding: 0 0 0 4px;
      }
    }
    .terms {
      font-weight: 500;
      font-size: 15px;
      line-height: 18px;
      letter-spacing: 0.02em;
      color: rgba(255, 255, 255, 0.6);
      span {
        color: #7f75e3;
        text-decoration: underline;
      }
    }
  }
}
